
.topItem {
  margin-bottom: 10px;
}
.topItem SPAN {
    font-size: 30px;
    line-height: 1.5;
}
.topItemIcon {
  vertical-align: top;
  padding-left: 20px;
  margin-right: -10px;
}
.icon-bracket {
  height: 26px;
  vertical-align: middle;
  padding-bottom: 8px;
  padding-left: 0;
}
.icon-bracket .arrow {
    height: 100%;
    vertical-align: middle;
    padding-bottom: 2px;
    width: 20px;
}
.arrow {
  transition: all .1s ease;
  text-align: center;
  display: inline-block;
  width: 11px;
  height: 21px;
  margin-top: 2px;
}
.arrowdown {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.item {
  cursor: pointer;
  width: 100%;
}
.bold {
  font-weight: bold;
}
.active {
  background: #81ff00;
  max-width: 772px;
}
.activeb {
  background: #81ff00;
  font-weight: bold;
}
.inline .content {
  max-width: 700px;
  white-space: normal;
  padding: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 16px;
  border: 6px solid #ccc;
  border-radius: 4px;
}
.vinline {
  max-width: 700px;
  white-space: normal;
  padding: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 4px;
  margin-bottom: 16px;
  height: 600px;
  width: 760px;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: auto;
}
.hshim {
  height: 15px;
}
.otitle {
  padding-left: 4px;
}
.child-items {
  margin: 0;
  padding: 0;
}
.unselected-sibling {
  background-color: yellow;
  height: 0;
  overflow: hidden;
  transition: height 4s ease;
}
.leo-box {
  display: none;
}



