
@charset "UTF-8";
.nestedviewer {
  height: 100%;
  margin-left: 6px;
}
.nestedviewer .item-box {
    border: 2px solid #ccc;
    margin-right: 3px;
    padding-left: 6px;
    overflow: hidden;
}
.nestedviewer ul {
    list-style-type: none;
    padding: 0;
    padding-left: 0;
    line-height: 1.4em;
    list-style-type: none;
    margin-bottom: 8px;
}
.nestedviewer li {
    white-space: nowrap;
    margin-bottom: 4px;
    margin-top: 4px;
}
.nestedviewer li > div {
    padding-left: 0;
}
.nestedviewer .leaf-button {
    width: 10px;
    padding-left: 4px;
    height: 10px;
    display: inline-block;
}
.nestedviewer .leaf-button::after {
    content: '○';
}



