
@import url(./assets/vue-instant.css);
.section-title {
  font-size: 140px;
  font-weight: bold;
}

/* title */
H1 {
  margin: 0;
}
.cls-1 {
  font-size: 122px;
  stroke: #000;
  stroke-width: 14px;
}
.cls-1, .cls-2 {
  stroke-miterlimit: 10;
  font-family: Verdana;
}
.cls-2 {
  font-size: 40px;
  fill: #f2f2f2;
  stroke: gray;
}
.cls-3 {
  letter-spacing: -0.02em;
}
.cls-4 {
  letter-spacing: -0.02em;
}

/* shakespeare */
.ptitle {
  text-align: center;
  font-size: 40px;
  margin-bottom: 40px;
}
.pdetail {
  text-align: center;
  height: 1.5em;
  font-size: 11px;
}
.speaker {
  margin-top: 10px;
  margin-bottom: 4px;
  font-weight: bold;
}
.line {
  margin-left: 40px;
}
.stagedir {
  font-style: italic;
  margin: 6px;
  font-size: .9em;
}

/* highlight */
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: white;
  color: black;
}
.hljs-comment, .hljs-quote, .hljs-variable {
  color: #008000;
}
.hljs-keyword,
.hljs-selector-tag,
.hljs-built_in,
.hljs-name,
.hljs-tag {
  color: #00f;
}
.hljs-string,
.hljs-title,
.hljs-section,
.hljs-attribute,
.hljs-literal,
.hljs-template-tag,
.hljs-template-variable,
.hljs-type,
.hljs-addition {
  color: #a31515;
}
.hljs-deletion,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-meta {
  color: #2b91af;
}
.hljs-doctag {
  color: #808080;
}
.hljs-attr {
  color: #f00;
}
.hljs-symbol,
.hljs-bullet,
.hljs-link {
  color: #00b0e8;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-strong {
  font-weight: bold;
}
HTML, BODY {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
H1 {
  margin: 4px;
}
#app {
  font-family: Avenir, Nunito, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
  padding: 0;
  height: 100%;
}
.right-pane .text, .inline .text {
  margin-top: 20px;
  border: none;
  background-color: transparent;
  resize: none;
  outline: none;
  font-size: 16px;
  white-space: pre-line;
  width: 100%;
}
.directive {
  color: #990000;
}
.md {
  margin-left: 10px;
  margin-top: 10px;
  max-width: 700px;
  padding-right: 10px;
}
.unselectable {
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
A, .link {
  color: #42b983;
  cursor: pointer;
  text-decoration: underline;
}



